"use client";

import { useState, useEffect } from "react";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Progress } from "@/components/ui/progress";
import { Button } from "@/components/ui/button";
import { Badge } from "@/components/ui/badge";

interface RiskFactor {
  id: string;
  name: string;
  category: 'financial' | 'operational' | 'regulatory' | 'market';
  impact: number; // 0-100
  probability: number; // 0-100
  trend: 'increasing' | 'decreasing' | 'stable';
  lastUpdated: string;
}

interface SimulationResult {
  overallRisk: number;
  confidence: number;
  timeHorizon: string;
  keyDrivers: string[];
  recommendations: string[];
}

const riskFactors: RiskFactor[] = [
  {
    id: "carbon-tax",
    name: "EU Carbon Tax Implementation",
    category: "regulatory",
    impact: 85,
    probability: 90,
    trend: "increasing",
    lastUpdated: "2 min ago"
  },
  {
    id: "supply-shortage",
    name: "Rare Earth Supply Shortage",
    category: "operational",
    impact: 75,
    probability: 70,
    trend: "stable",
    lastUpdated: "5 min ago"
  },
  {
    id: "market-volatility",
    name: "EV Market Volatility",
    category: "market",
    impact: 60,
    probability: 80,
    trend: "increasing",
    lastUpdated: "1 min ago"
  },
  {
    id: "currency-risk",
    name: "EUR/USD Exchange Risk",
    category: "financial",
    impact: 45,
    probability: 65,
    trend: "decreasing",
    lastUpdated: "3 min ago"
  },
  {
    id: "competition",
    name: "Increased Competition",
    category: "market",
    impact: 55,
    probability: 85,
    trend: "increasing",
    lastUpdated: "4 min ago"
  }
];

export default function SimulationEngine() {
  const [isRunning, setIsRunning] = useState(false);
  const [currentRisk, setCurrentRisk] = useState(72);
  const [simulationProgress, setSimulationProgress] = useState(0);
  const [factors, setFactors] = useState(riskFactors);
  const [result, setResult] = useState<SimulationResult>({
    overallRisk: 72,
    confidence: 87,
    timeHorizon: "6 months",
    keyDrivers: ["EU Carbon Tax", "Supply Chain Disruption", "Market Competition"],
    recommendations: [
      "Diversify supply chain to reduce dependency",
      "Accelerate India market entry",
      "Invest in battery recycling technology"
    ]
  });

  // Simulate real-time risk calculation
  useEffect(() => {
    if (!isRunning) return;

    const interval = setInterval(() => {
      setSimulationProgress(prev => {
        if (prev >= 100) {
          setIsRunning(false);
          // Update risk factors with new values
          setFactors(prev => prev.map(factor => ({
            ...factor,
            impact: Math.max(0, Math.min(100, factor.impact + (Math.random() - 0.5) * 10)),
            probability: Math.max(0, Math.min(100, factor.probability + (Math.random() - 0.5) * 8)),
            lastUpdated: "Just now"
          })));
          
          // Recalculate overall risk
          const newRisk = Math.round(
            factors.reduce((acc, factor) => 
              acc + (factor.impact * factor.probability / 100), 0
            ) / factors.length
          );
          setCurrentRisk(newRisk);
          
          return 0;
        }
        return prev + 2;
      });
    }, 100);

    return () => clearInterval(interval);
  }, [isRunning, factors]);

  const runSimulation = () => {
    setIsRunning(true);
    setSimulationProgress(0);
  };

  const getCategoryColor = (category: string) => {
    const colors = {
      financial: "bg-green-900/20 border-green-800/30 text-green-400",
      operational: "bg-orange-900/20 border-orange-800/30 text-orange-400",
      regulatory: "bg-red-900/20 border-red-800/30 text-red-400",
      market: "bg-blue-900/20 border-blue-800/30 text-blue-400"
    };
    return colors[category as keyof typeof colors];
  };

  const getTrendIcon = (trend: string) => {
    const icons = {
      increasing: "üìà",
      decreasing: "üìâ",
      stable: "‚û°Ô∏è"
    };
    return icons[trend as keyof typeof icons];
  };

  const getRiskLevel = (risk: number) => {
    if (risk >= 80) return { label: "CRITICAL", color: "bg-red-500" };
    if (risk >= 60) return { label: "HIGH", color: "bg-orange-500" };
    if (risk >= 40) return { label: "MEDIUM", color: "bg-yellow-500" };
    return { label: "LOW", color: "bg-green-500" };
  };

  const riskLevel = getRiskLevel(currentRisk);

  return (
    <div className="space-y-6">
      {/* Main Simulation Control */}
      <Card className="bg-slate-800/50 border-slate-700">
        <CardHeader>
          <div className="flex items-center justify-between">
            <CardTitle className="flex items-center space-x-2">
              <div className="w-3 h-3 bg-purple-500 rounded-full"></div>
              <span>Multi-Agent Simulation Engine</span>
            </CardTitle>
            <Button 
              onClick={runSimulation}
              disabled={isRunning}
              className="bg-purple-600 hover:bg-purple-700"
            >
              {isRunning ? "Running..." : "Run Simulation"}
            </Button>
          </div>
        </CardHeader>
        <CardContent>
          <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
            {/* Current Risk Index */}
            <div className="bg-slate-900/50 p-6 rounded-lg">
              <h3 className="text-slate-400 text-sm font-medium mb-2">OVERALL RISK INDEX</h3>
              <div className="flex items-center space-x-4 mb-4">
                <div className="text-4xl font-bold text-white">{currentRisk}</div>
                <div className="text-slate-400">/100</div>
                <Badge className={`${riskLevel.color} text-white`}>
                  {riskLevel.label}
                </Badge>
              </div>
              <Progress value={currentRisk} className="h-3 mb-2" />
              <div className="text-xs text-slate-400">
                Confidence: {result.confidence}% ‚Ä¢ Horizon: {result.timeHorizon}
              </div>
            </div>

            {/* Simulation Progress */}
            <div className="bg-slate-900/50 p-6 rounded-lg">
              <h3 className="text-slate-400 text-sm font-medium mb-2">SIMULATION STATUS</h3>
              {isRunning ? (
                <div>
                  <div className="text-2xl font-bold text-purple-400 mb-2">Processing...</div>
                  <Progress value={simulationProgress} className="h-3 mb-2" />
                  <div className="text-xs text-slate-400">
                    Analyzing {factors.length} risk factors ‚Ä¢ {Math.round(simulationProgress)}% complete
                  </div>
                </div>
              ) : (
                <div>
                  <div className="text-2xl font-bold text-green-400 mb-2">Ready</div>
                  <div className="text-sm text-slate-300 mb-2">Last run: {new Date().toLocaleTimeString()}</div>
                  <div className="text-xs text-slate-400">
                    Click "Run Simulation" to update risk assessment
                  </div>
                </div>
              )}
            </div>

            {/* Key Metrics */}
            <div className="bg-slate-900/50 p-6 rounded-lg">
              <h3 className="text-slate-400 text-sm font-medium mb-4">KEY METRICS</h3>
              <div className="space-y-3">
                <div className="flex justify-between">
                  <span className="text-slate-300 text-sm">Active Factors:</span>
                  <span className="text-white font-semibold">{factors.length}</span>
                </div>
                <div className="flex justify-between">
                  <span className="text-slate-300 text-sm">High Impact:</span>
                  <span className="text-red-400 font-semibold">
                    {factors.filter(f => f.impact >= 70).length}
                  </span>
                </div>
                <div className="flex justify-between">
                  <span className="text-slate-300 text-sm">Increasing Trends:</span>
                  <span className="text-orange-400 font-semibold">
                    {factors.filter(f => f.trend === 'increasing').length}
                  </span>
                </div>
                <div className="flex justify-between">
                  <span className="text-slate-300 text-sm">Avg Probability:</span>
                  <span className="text-white font-semibold">
                    {Math.round(factors.reduce((acc, f) => acc + f.probability, 0) / factors.length)}%
                  </span>
                </div>
              </div>
            </div>
          </div>
        </CardContent>
      </Card>

      {/* Risk Factors Breakdown */}
      <Card className="bg-slate-800/50 border-slate-700">
        <CardHeader>
          <CardTitle>Risk Factors Analysis</CardTitle>
        </CardHeader>
        <CardContent>
          <div className="space-y-4">
            {factors.map((factor) => (
              <div key={factor.id} className={`p-4 rounded-lg border ${getCategoryColor(factor.category)}`}>
                <div className="flex items-center justify-between mb-3">
                  <div className="flex items-center space-x-3">
                    <h4 className="font-semibold text-white">{factor.name}</h4>
                    <Badge variant="outline" className="text-xs capitalize">
                      {factor.category}
                    </Badge>
                    <span className="text-lg">{getTrendIcon(factor.trend)}</span>
                  </div>
                  <div className="text-xs text-slate-400">{factor.lastUpdated}</div>
                </div>
                
                <div className="grid grid-cols-2 gap-4">
                  <div>
                    <div className="flex justify-between text-sm mb-1">
                      <span className="text-slate-400">Impact</span>
                      <span className="text-white font-medium">{factor.impact}%</span>
                    </div>
                    <Progress value={factor.impact} className="h-2" />
                  </div>
                  <div>
                    <div className="flex justify-between text-sm mb-1">
                      <span className="text-slate-400">Probability</span>
                      <span className="text-white font-medium">{factor.probability}%</span>
                    </div>
                    <Progress value={factor.probability} className="h-2" />
                  </div>
                </div>
                
                <div className="mt-2 text-right">
                  <span className="text-xs text-slate-400">Risk Score: </span>
                  <span className="text-white font-semibold">
                    {Math.round((factor.impact * factor.probability) / 100)}
                  </span>
                </div>
              </div>
            ))}
          </div>
        </CardContent>
      </Card>

      {/* AI Recommendations */}
      <Card className="bg-slate-800/50 border-slate-700">
        <CardHeader>
          <CardTitle className="flex items-center space-x-2">
            <div className="w-3 h-3 bg-green-500 rounded-full"></div>
            <span>AI-Generated Recommendations</span>
          </CardTitle>
        </CardHeader>
        <CardContent>
          <div className="space-y-3">
            {result.recommendations.map((rec, index) => (
              <div key={index} className="flex items-start space-x-3 p-3 bg-slate-900/50 rounded-lg">
                <div className="w-6 h-6 bg-green-500 rounded-full flex items-center justify-center text-white text-sm font-bold">
                  {index + 1}
                </div>
                <div className="flex-1">
                  <p className="text-white">{rec}</p>
                </div>
              </div>
            ))}
          </div>
        </CardContent>
      </Card>
    </div>
  );
}
