"use client";

import { useState, useEffect } from "react";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";

interface MonitoringItem {
  id: string;
  title: string;
  content: string;
  timestamp: string;
  type: 'sentiment' | 'market' | 'news' | 'regulatory';
  severity: 'low' | 'medium' | 'high';
  trend: 'up' | 'down' | 'stable';
}

const initialData: MonitoringItem[] = [
  {
    id: "1",
    title: "Social Sentiment",
    content: '"Carbon tax unfair" trending in EU',
    timestamp: "Updated 2 min ago",
    type: "sentiment",
    severity: "high",
    trend: "down"
  },
  {
    id: "2",
    title: "Market Data",
    content: "Lithium spot price +14% (30 days)",
    timestamp: "Live feed",
    type: "market",
    severity: "medium",
    trend: "up"
  },
  {
    id: "3",
    title: "News Scraping",
    content: "Chile investing in lithium expansion",
    timestamp: "Reuters ‚Ä¢ 1 hour ago",
    type: "news",
    severity: "low",
    trend: "up"
  },
  {
    id: "4",
    title: "Regulatory Tracker",
    content: "EU vote on stricter EV regulations pending",
    timestamp: "Expected: Q2 2024",
    type: "regulatory",
    severity: "high",
    trend: "down"
  }
];

const additionalUpdates: MonitoringItem[] = [
  {
    id: "5",
    title: "Supply Chain Alert",
    content: "Shanghai port delays affecting battery shipments",
    timestamp: "Breaking ‚Ä¢ 30 sec ago",
    type: "news",
    severity: "high",
    trend: "down"
  },
  {
    id: "6",
    title: "Market Sentiment",
    content: "Tesla stock volatility increases 23%",
    timestamp: "Live feed",
    type: "market",
    severity: "medium",
    trend: "down"
  },
  {
    id: "7",
    title: "Social Media Buzz",
    content: "EV adoption hashtags trending globally",
    timestamp: "Twitter ‚Ä¢ 5 min ago",
    type: "sentiment",
    severity: "low",
    trend: "up"
  },
  {
    id: "8",
    title: "Legal Update",
    content: "New battery recycling mandate proposed",
    timestamp: "EU Commission ‚Ä¢ 15 min ago",
    type: "regulatory",
    severity: "medium",
    trend: "stable"
  }
];

export default function LiveMonitor() {
  const [items, setItems] = useState<MonitoringItem[]>(initialData);
  const [isLive, setIsLive] = useState(true);
  const [lastUpdate, setLastUpdate] = useState(new Date());

  useEffect(() => {
    if (!isLive) return;

    const interval = setInterval(() => {
      // Simulate new data coming in
      const randomUpdate = additionalUpdates[Math.floor(Math.random() * additionalUpdates.length)];
      const newItem = {
        ...randomUpdate,
        id: `${Date.now()}`,
        timestamp: `Live ‚Ä¢ ${Math.floor(Math.random() * 60)} sec ago`
      };

      setItems(prev => [newItem, ...prev.slice(0, 7)]); // Keep only 8 items
      setLastUpdate(new Date());
    }, 8000); // Update every 8 seconds

    return () => clearInterval(interval);
  }, [isLive]);

  const getTypeIcon = (type: string) => {
    const icons = {
      sentiment: "üìä",
      market: "üìà",
      news: "üîç",
      regulatory: "‚öñÔ∏è"
    };
    return icons[type as keyof typeof icons];
  };

  const getTypeColor = (type: string) => {
    const colors = {
      sentiment: "bg-red-900/20 border-red-800/30 text-red-400",
      market: "bg-orange-900/20 border-orange-800/30 text-orange-400",
      news: "bg-blue-900/20 border-blue-800/30 text-blue-400",
      regulatory: "bg-purple-900/20 border-purple-800/30 text-purple-400"
    };
    return colors[type as keyof typeof colors];
  };

  const getSeverityColor = (severity: string) => {
    const colors = {
      low: "bg-green-900/50 text-green-300",
      medium: "bg-yellow-900/50 text-yellow-300",
      high: "bg-red-900/50 text-red-300"
    };
    return colors[severity as keyof typeof colors];
  };

  const getTrendIcon = (trend: string) => {
    const icons = {
      up: "‚ÜóÔ∏è",
      down: "‚ÜòÔ∏è",
      stable: "‚û°Ô∏è"
    };
    return icons[trend as keyof typeof icons];
  };

  return (
    <Card className="bg-slate-800/50 border-slate-700">
      <CardHeader>
        <div className="flex items-center justify-between">
          <CardTitle className="flex items-center space-x-2">
            <div className={`w-3 h-3 rounded-full ${isLive ? 'bg-red-500 animate-pulse' : 'bg-gray-500'}`}></div>
            <span>Live Internet-Driven Monitoring</span>
          </CardTitle>
          <div className="flex items-center space-x-2">
            <Badge variant="outline" className="text-xs">
              Last update: {lastUpdate.toLocaleTimeString()}
            </Badge>
            <button
              onClick={() => setIsLive(!isLive)}
              className={`px-3 py-1 rounded text-xs font-medium transition-colors ${
                isLive 
                  ? 'bg-red-900/50 text-red-300 hover:bg-red-900/70' 
                  : 'bg-green-900/50 text-green-300 hover:bg-green-900/70'
              }`}
            >
              {isLive ? 'LIVE' : 'PAUSED'}
            </button>
          </div>
        </div>
      </CardHeader>
      <CardContent>
        <div className="grid grid-cols-1 md:grid-cols-2 gap-4 max-h-96 overflow-y-auto">
          {items.map((item, index) => (
            <div
              key={item.id}
              className={`p-4 rounded-lg border transition-all duration-500 ${getTypeColor(item.type)} ${
                index === 0 && isLive ? 'ring-2 ring-blue-500/50 animate-pulse' : ''
              }`}
            >
              <div className="flex items-start space-x-3">
                <div className={`w-8 h-8 rounded-lg flex items-center justify-center text-xl ${
                  item.type === 'sentiment' ? 'bg-red-500/20' :
                  item.type === 'market' ? 'bg-orange-500/20' :
                  item.type === 'news' ? 'bg-blue-500/20' :
                  'bg-purple-500/20'
                }`}>
                  {getTypeIcon(item.type)}
                </div>
                <div className="flex-1 min-w-0">
                  <div className="flex items-center justify-between mb-1">
                    <h4 className={`font-semibold text-sm ${
                      item.type === 'sentiment' ? 'text-red-400' :
                      item.type === 'market' ? 'text-orange-400' :
                      item.type === 'news' ? 'text-blue-400' :
                      'text-purple-400'
                    }`}>
                      {item.title}
                    </h4>
                    <div className="flex items-center space-x-1">
                      <Badge className={`text-xs px-2 py-0 ${getSeverityColor(item.severity)}`}>
                        {item.severity.toUpperCase()}
                      </Badge>
                      <span className="text-lg">{getTrendIcon(item.trend)}</span>
                    </div>
                  </div>
                  <p className="text-white text-sm mb-2 line-clamp-2">{item.content}</p>
                  <div className="flex items-center justify-between">
                    <p className="text-slate-400 text-xs">{item.timestamp}</p>
                    {index === 0 && isLive && (
                      <Badge variant="outline" className="text-xs animate-pulse">
                        NEW
                      </Badge>
                    )}
                  </div>
                </div>
              </div>
            </div>
          ))}
        </div>

        {/* Summary Stats */}
        <div className="mt-6 grid grid-cols-2 md:grid-cols-4 gap-4">
          <div className="bg-slate-900/50 p-3 rounded-lg text-center">
            <div className="text-2xl font-bold text-red-400">
              {items.filter(i => i.severity === 'high').length}
            </div>
            <div className="text-xs text-slate-400">High Priority</div>
          </div>
          <div className="bg-slate-900/50 p-3 rounded-lg text-center">
            <div className="text-2xl font-bold text-orange-400">
              {items.filter(i => i.trend === 'up').length}
            </div>
            <div className="text-xs text-slate-400">Positive Trends</div>
          </div>
          <div className="bg-slate-900/50 p-3 rounded-lg text-center">
            <div className="text-2xl font-bold text-blue-400">
              {items.filter(i => i.type === 'market').length}
            </div>
            <div className="text-xs text-slate-400">Market Signals</div>
          </div>
          <div className="bg-slate-900/50 p-3 rounded-lg text-center">
            <div className="text-2xl font-bold text-green-400">
              {Math.round((items.filter(i => i.trend === 'up').length / items.length) * 100)}%
            </div>
            <div className="text-xs text-slate-400">Positive Ratio</div>
          </div>
        </div>
      </CardContent>
    </Card>
  );
}
