"use client";

import { useState, useEffect } from "react";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";

interface NetworkNode {
  id: string;
  label: string;
  type: 'risk' | 'warning' | 'opportunity' | 'company';
  x: number;
  y: number;
  connections: string[];
}

interface Connection {
  from: string;
  to: string;
  strength: number;
  type: 'negative' | 'positive' | 'neutral';
}

const networkData: NetworkNode[] = [
  { id: "carbon-tax", label: "EU Carbon Tax", type: "risk", x: 150, y: 100, connections: ["globalauto"] },
  { id: "globalauto", label: "GlobalAuto", type: "company", x: 400, y: 200, connections: ["supply-chain", "india-opportunity"] },
  { id: "supply-chain", label: "Supply Chain", type: "warning", x: 650, y: 150, connections: [] },
  { id: "india-opportunity", label: "India Opportunity", type: "opportunity", x: 400, y: 320, connections: [] },
  { id: "rare-earth", label: "Rare Earth Shortage", type: "risk", x: 150, y: 280, connections: ["globalauto"] }
];

const connections: Connection[] = [
  { from: "carbon-tax", to: "globalauto", strength: 0.8, type: "negative" },
  { from: "globalauto", to: "supply-chain", strength: 0.6, type: "negative" },
  { from: "globalauto", to: "india-opportunity", strength: 0.7, type: "positive" },
  { from: "rare-earth", to: "globalauto", strength: 0.9, type: "negative" }
];

export default function RippleNetwork() {
  const [selectedNode, setSelectedNode] = useState<string | null>(null);
  const [animationPhase, setAnimationPhase] = useState(0);

  useEffect(() => {
    const interval = setInterval(() => {
      setAnimationPhase((prev) => (prev + 1) % 100);
    }, 100);
    return () => clearInterval(interval);
  }, []);

  const getNodeColor = (type: string, isSelected: boolean) => {
    const colors = {
      risk: isSelected ? '#ef4444' : '#dc2626',
      warning: isSelected ? '#f59e0b' : '#d97706',
      opportunity: isSelected ? '#10b981' : '#059669',
      company: isSelected ? '#3b82f6' : '#2563eb'
    };
    return colors[type as keyof typeof colors];
  };

  const getConnectionColor = (type: string) => {
    const colors = {
      negative: '#ef4444',
      positive: '#10b981',
      neutral: '#6b7280'
    };
    return colors[type as keyof typeof colors];
  };

  return (
    <Card className="bg-slate-800/50 border-slate-700">
      <CardHeader>
        <CardTitle className="flex items-center space-x-2">
          <div className="w-3 h-3 bg-cyan-500 rounded-full"></div>
          <span>Interactive Ripple Effect Network</span>
        </CardTitle>
      </CardHeader>
      <CardContent>
        <div className="relative h-96 bg-slate-900/50 rounded-lg overflow-hidden">
          <svg className="w-full h-full" viewBox="0 0 800 400">
            {/* Animated background grid */}
            <defs>
              <pattern id="grid" width="40" height="40" patternUnits="userSpaceOnUse">
                <path d="M 40 0 L 0 0 0 40" fill="none" stroke="#1e293b" strokeWidth="1" opacity="0.3"/>
              </pattern>
            </defs>
            <rect width="100%" height="100%" fill="url(#grid)" />

            {/* Connections with animation */}
            {connections.map((conn, index) => {
              const fromNode = networkData.find(n => n.id === conn.from);
              const toNode = networkData.find(n => n.id === conn.to);
              if (!fromNode || !toNode) return null;

              const isActive = selectedNode === conn.from || selectedNode === conn.to;
              
              return (
                <g key={`${conn.from}-${conn.to}`}>
                  {/* Connection line */}
                  <line
                    x1={fromNode.x}
                    y1={fromNode.y}
                    x2={toNode.x}
                    y2={toNode.y}
                    stroke={getConnectionColor(conn.type)}
                    strokeWidth={isActive ? "3" : "2"}
                    opacity={isActive ? 0.9 : 0.6}
                    strokeDasharray={conn.type === 'negative' ? "5,5" : "none"}
                  />
                  
                  {/* Animated pulse along connection */}
                  {isActive && (
                    <circle
                      r="4"
                      fill={getConnectionColor(conn.type)}
                      opacity="0.8"
                    >
                      <animateMotion
                        dur="2s"
                        repeatCount="indefinite"
                        path={`M${fromNode.x},${fromNode.y} L${toNode.x},${toNode.y}`}
                      />
                    </circle>
                  )}
                </g>
              );
            })}

            {/* Nodes */}
            {networkData.map((node) => {
              const isSelected = selectedNode === node.id;
              const isConnected = selectedNode && connections.some(
                c => (c.from === selectedNode && c.to === node.id) || 
                     (c.to === selectedNode && c.from === node.id)
              );

              return (
                <g key={node.id}>
                  {/* Node glow effect */}
                  {isSelected && (
                    <circle
                      cx={node.x}
                      cy={node.y}
                      r="35"
                      fill={getNodeColor(node.type, false)}
                      opacity="0.2"
                      className="animate-pulse"
                    />
                  )}
                  
                  {/* Main node */}
                  <circle
                    cx={node.x}
                    cy={node.y}
                    r={isSelected ? "25" : "20"}
                    fill={getNodeColor(node.type, isSelected)}
                    stroke={isConnected ? "#ffffff" : "transparent"}
                    strokeWidth="2"
                    className="cursor-pointer transition-all duration-200 hover:r-[23]"
                    onClick={() => setSelectedNode(selectedNode === node.id ? null : node.id)}
                  />
                  
                  {/* Node label */}
                  <text
                    x={node.x}
                    y={node.y - 35}
                    textAnchor="middle"
                    className="fill-white text-sm font-medium pointer-events-none"
                    opacity={isSelected || isConnected ? 1 : 0.8}
                  >
                    {node.label}
                  </text>
                  
                  {/* Node type indicator */}
                  <text
                    x={node.x}
                    y={node.y + 5}
                    textAnchor="middle"
                    className="fill-white text-xs pointer-events-none"
                    opacity="0.9"
                  >
                    {node.type === 'risk' && '‚ö†Ô∏è'}
                    {node.type === 'warning' && 'üî∂'}
                    {node.type === 'opportunity' && 'üåü'}
                    {node.type === 'company' && 'üè¢'}
                  </text>
                </g>
              );
            })}
          </svg>

          {/* Node details panel */}
          {selectedNode && (
            <div className="absolute top-4 right-4 bg-slate-800/90 backdrop-blur-sm border border-slate-600 rounded-lg p-4 max-w-xs">
              {(() => {
                const node = networkData.find(n => n.id === selectedNode);
                if (!node) return null;
                
                const nodeConnections = connections.filter(
                  c => c.from === selectedNode || c.to === selectedNode
                );

                return (
                  <div>
                    <h4 className="font-semibold text-white mb-2">{node.label}</h4>
                    <p className="text-slate-300 text-sm mb-3 capitalize">{node.type} Node</p>
                    
                    {nodeConnections.length > 0 && (
                      <div>
                        <h5 className="text-slate-400 text-xs font-medium mb-2">CONNECTIONS</h5>
                        <div className="space-y-1">
                          {nodeConnections.map((conn, idx) => {
                            const connectedNodeId = conn.from === selectedNode ? conn.to : conn.from;
                            const connectedNode = networkData.find(n => n.id === connectedNodeId);
                            return (
                              <div key={idx} className="flex items-center justify-between text-xs">
                                <span className="text-slate-300">{connectedNode?.label}</span>
                                <span className={`px-2 py-1 rounded text-xs ${
                                  conn.type === 'negative' ? 'bg-red-900/50 text-red-300' :
                                  conn.type === 'positive' ? 'bg-green-900/50 text-green-300' :
                                  'bg-gray-900/50 text-gray-300'
                                }`}>
                                  {Math.round(conn.strength * 100)}%
                                </span>
                              </div>
                            );
                          })}
                        </div>
                      </div>
                    )}
                  </div>
                );
              })()}
            </div>
          )}
        </div>
        
        <div className="mt-4 flex items-center justify-between text-sm text-slate-400">
          <span>Click nodes to explore connections</span>
          <div className="flex items-center space-x-4">
            <div className="flex items-center space-x-2">
              <div className="w-3 h-3 bg-red-500 rounded-full"></div>
              <span>Risk</span>
            </div>
            <div className="flex items-center space-x-2">
              <div className="w-3 h-3 bg-orange-500 rounded-full"></div>
              <span>Warning</span>
            </div>
            <div className="flex items-center space-x-2">
              <div className="w-3 h-3 bg-green-500 rounded-full"></div>
              <span>Opportunity</span>
            </div>
          </div>
        </div>
      </CardContent>
    </Card>
  );
}
